@import "../global.scss";

.header {
  padding: 2rem;
  position: relative;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;

  &__title {
    font-weight: 600;
    font-size: 3rem;
    color: #fff;
  }

  &__search {
    input {
      position: absolute;
      top: 194%;
      left: 5%;
      background: transparent;
      border: 1px solid linear-gradient(270deg, #d79acd 0%, #7884cf 103.79%);
      padding: 1rem 2rem;
      border-radius: 10px;
      margin-left: 0rem;
      width: 40rem;
      color: white;

      @include mobile {
        width: 20rem;
      }
    }
  }
}

.sortBy {
  padding: 1rem;
  max-width: 25rem;
  margin: auto;
  margin-left: 44rem;
  margin-right: 11rem;

  .css-1okebmr-indicatorSeparator {
    display: none;
  }

  .css-1s2u09g-control {
    color: white;
    background-color: transparent;
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-color: hsl(0, 0%, 80%);
    border-radius: 10px;
    border-style: solid;
    border-width: 1px;
    cursor: default;
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex-wrap: wrap;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    justify-content: space-between;
    min-height: 38px;
    outline: 0 !important;
    position: relative;
    -webkit-transition: all 100ms;
    transition: all 100ms;
    box-sizing: border-box;

    @include mobile {
      display: none;
    }

    #placeholder {
      color: white;
    }
  }
  .css-6j8wv5-Input {
    margin: 2px;
    padding-bottom: 10px;
    padding-top: 10px;
    visibility: visible;
    color: hsl(0, 0%, 20%);
    -webkit-flex: 1 1 auto;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    display: inline-grid;
    grid-area: 1/1/2/3;
    grid-template-columns: 0 min-content;
    box-sizing: border-box;
  }

  .select-filter {
    margin-top: 1rem;
  }
}

