.users-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 2rem;
  padding-left: 10px;
  @include mobile {
    // display: grid;
    // grid-auto-columns: auto auto auto auto;
    // gap: 10px;
    margin-top: 4rem;
  }

  .user {
    max-width: 300px;
    border: 1px solid white;
    background: #212121;
    max-height: 400px;

    @include mobile {
      max-height: 210px;
    }

    &__name {
      font-weight: 200;
      font-size: 22px;
      line-height: 24px;
      color: #fff;
      letter-spacing: 1px;
      margin-top: 2rem;

      @include mobile {
        font-weight: 200;
        font-size: 13px;
        line-height: 15px;
        margin-top: 0rem;
        padding-top: 1rem;
      }
    }

    &__image {
      width: 100%;
      height: auto;
      border: none;
      margin-bottom: 2rem;

      img {
        @include mobile {
          max-height: 100%;
        }
      }
    }

    .card-body {
      margin-left: 0;
      margin-top: -2rem;
    }
    &__details {
      position: relative;
      margin-bottom: 0rem;
      span {
        display: block;
        font-weight: 300;
        font-size: 20px;
        line-height: 24px;
        // opacity: 0.6;
        padding: 0;
        margin: 0;
        color: white;
        line-height: 1.5;
      }
      .position {
        position: absolute;
        top: -6rem;
        /* min-width: 100px; */
        left: -3rem;
        box-sizing: border-box;
        background: white;
        color: black;
        padding-right: -1px;
        display: inline-block;
        padding-left: 13px;
        padding-right: 13px;

        @include mobile {
          top: -3.5rem;
          left: 0rem;
          font-size: 13px;
        }
      }
      .social-icons {
        margin-left: -2rem;
        margin-top: 1rem;
        padding-top: 5px;
        color: rgba(255, 255, 255, 0.541);
        @include mobile {
          margin-top: -1rem;
          display: inline-flex;
        }

        .icons {
          margin-left: 10px;

          @include mobile {
            margin-left: 0px;
            margin-right: 0px;
            height: 22px;
          }
        }
      }
    }
  }
}
